<template>
  <div class="task-view">
    <SentenceList v-bind:taskId="taskId"/>
  </div>
</template>

<script>
import SentenceList from '@/components/SentenceList.vue';

export default {
  name: 'Task',
  title: (context) => `Task: ${context.task.name}`,
  components: {
    SentenceList,
  },
  computed: {
    taskId() {
      return this.$route.params.taskId;
    },
    task() {
      return this.$store.state.tasks[this.taskId];
    },
  },
};
</script>
