<template>
  <div class="sentence-list">
    <SentenceListCard v-for="(sentence, index) in sentences"
    v-bind:sentence="sentence"
    v-bind:index="index"
    v-bind:key="index"
    v-bind:taskId="taskId"/>
  </div>
</template>

<script>
import SentenceListCard from '@/components/SentenceListCard.vue';

export default {
  name: 'SentenceList',
  components: {
    SentenceListCard,
  },
  props: {
    taskId: String,
  },
  computed: {
    sentences() {
      return this.$store.state.tasks[this.taskId].sentences;
    },
  },
};
</script>
