<template>
  <div class="sentence-view">
    <div class="scrollable-area">
      <div class="sentence-area">
        <div class="index">
          {{ Number(sentenceId) + 1 }}
        </div>
        <div class="sentence">
          <pre>{{ sentence }}</pre>
        </div>
      </div>
      <div class="question-area">
        <div>
          <h1>Questions</h1>
        </div>
        <Question
          v-for="(question, index) in questions"
          v-bind:question="question"
          v-bind:index="index"
          v-bind:key="sentenceId + '-' + index"
        />
      </div>
    </div>
    <div class="action-area">
      <router-link
        :to="{ name: 'Task', params: { taskId: taskId } }"
        tag="button"
        >Sentences</router-link
      >

      <router-link :to="goToHome" tag="button">Home</router-link>

      <router-link :to="this.nextRoute" tag="button">{{
        this.hasNextSentence ? 'Next' : 'Done'
      }}</router-link>
    </div>
  </div>
</template>

<script>
import Question from '@/components/question/Question.vue';

export default {
  name: 'Sentence',
  title: 'Sentence',
  components: {
    Question,
  },
  computed: {
    taskId() {
      return this.$route.params.taskId;
    },
    task() {
      return this.$store.state.tasks[this.taskId];
    },
    sentenceId() {
      return this.$route.params.sentenceId;
    },
    sentence() {
      return this.task.sentences[this.sentenceId];
    },
    questions() {
      return this.task.questions;
    },
    hasNextSentence() {
      return this.sentenceId < this.task.sentences.length - 1;
    },
    goToHome() {
      return { name: 'Home' };
    },
    nextSentence() {
      return {
        name: 'Sentence',
        params: {
          taskId: String(this.taskId),
          sentenceId: Number(this.sentenceId) + 1,
        },
      };
    },
    nextRoute() {
      if (this.hasNextSentence) {
        return this.nextSentence;
      }

      return this.goToHome;
    },
  },
};
</script>

