<template>
  <div class="question">
    <h2>{{index + 1 }}. {{ question.question }}</h2>
    <QuestionSelect
      v-if="isSelect"
      v-bind:question="question"
      v-bind:index="index"
    />
    <QuestionNumber
      v-if="isNumber"
      v-bind:question="question"
      v-bind:index="index"
    />
  </div>
</template>

<script>
import * as questionTypes from '@/components/question/questionTypes';
import QuestionSelect from '@/components/question/QuestionSelect.vue';
import QuestionNumber from '@/components/question/QuestionNumber.vue';

export default {
  name: 'Question',
  props: {
    index: Number,
    question: Object,
  },
  components: {
    QuestionSelect,
    QuestionNumber,
  },
  computed: {
    isSelect() {
      return questionTypes.SELECT === this.question.type;
    },
    isNumber() {
      return questionTypes.NUMBER === this.question.type;
    },
  },
};
</script>

<style scoped>
.question {
  border: solid;
  padding: 5px;
  margin: 5px;
}
</style>
